.container{
	font-size: 25px;
	box-sizing: border-box;	
	font-family: 'Redressed', cursive;
	color: white;
	padding: 0%;
	margin: 0%;
	bottom: 0px;
	background-image: url("./background.jpg");
	/* Full height */
	height: 100%; 
	/* Center and scale the image nicely */
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}
.header{
	background-image: linear-gradient(to right, #ff6666,#ff6666) ;
	color:white;
	padding: 1%;
	margin: 0%;
}
.header h3{
	font-size: 35px;
	font-weight: lighter;
	padding: 0%;
	margin: 0%;
}
.sec1{
	margin-top: 5%;
    margin-bottom: 2%;
	padding-top: 1%;
	font-weight: 400;
}
.horizontal-list{
	padding: 3%;
}
.sec2{
	margin-top: 2%;
}
.buttons{
	margin-left: 2%;
	margin-right: 2%;
}
.image-sec{
	text-align: center;
	vertical-align: middle;
	width: auto;
	padding-top: 3%;
	padding-bottom: 3%;
}

@media only screen and (max-width:600px){
	.container{
		font-size: 20px;
	}

	.header h3{
		font-size: 30px;
	}
	.sec1{
		margin-top: 5%;
		margin-bottom: 2%;
		padding-top: 5%;
		font-weight: 400;
	}
	.horizontal-list{
		padding: 5%;
	}
	.sec2{
		margin-top: 4%;
	}
	.buttons{
		font-size: 15px;
		margin-left: 2%;
		margin-right: 2%;
	}
	.image-sec{
		width: 100%;
		height: 100%;
		margin-top: 5%;
		margin-bottom: 5%;
		text-align: center;
		vertical-align: middle;
	}
	.image-sec img{
		width:70%;
		height:50%;
	}

}